import React, { useState } from 'react'
import { Modal, Form, Input, Select, Button, message, Row, Col, Alert } from 'antd'
import { JsonEditor as Editor } from 'jsoneditor-react';
import { forkTask } from '../../api/index'

const Fork = (props) => {
  const { onClose, onSave, config, pluginInstallList } = props

  const [clazzList, setClazzList] = useState([])
  const [form] = Form.useForm();

  const params = {
    ...config,
    configJson: JSON.parse(config.configJson),
    settingJsonConfig: JSON.parse(config.settingJsonConfig)
  }
  const tailLayout = {
    wrapperCol: { offset: 0, span: 24 },
  };

  const onFinish = async (values) => {
    const obj = {
      ...params,
      ...values,
      configJson: JSON.stringify(params.configJson),
      settingJsonConfig: JSON.stringify(params.settingJsonConfig)
    }
    const { data, result, msg } = await forkTask(obj)
    if (result) {
      message.success('修改成功')
      onSave()
      onClose()
    } else {
      message.error(msg)
    }
  }

  const onReset = () => {
    onClose()
  }

  const onChange = (v, k) => {
    params[k] = v
  }

  const  onSelectChange = (value) => {
    pluginInstallList.forEach(plugin=>{
      if (plugin.pluginId === value) {
        const curList = []
        const { dataSinkCreatorInfos = [] } = plugin;
        (dataSinkCreatorInfos || []).forEach(dataSink=>{
          curList.push(dataSink.clazz);
        })
        setClazzList([...curList])
      }
    })
  }

  const genTaskSetting = () => {
    return <React.Fragment>
      <div className="custom-title">任务设置</div>
      <Editor
        value={params.settingJsonConfig}
        onChange={(value) => onChange(value, 'settingJsonConfig')}
      />
    </React.Fragment>
  }

  const genTaskAction = () => {
    return <React.Fragment>
      <div className="custom-title">任务行为</div>
      <Editor
        value={params.configJson}
        onChange={(value) => onChange(value, 'configJson')}
      />
    </React.Fragment>
  }

  return <Modal
    visible={config.pluginId}
    title={`Fork任务`}
    onCancel={() => onClose()}
    width={800}
    footer={null}
  >
    <Form
      form={form}
      initialValues={params}
      labelCol={{ span: 6 }}
      wrapperCol={{ span: 16 }}
      onFinish={onFinish}
    >
      <Row>
        <Col span={10}>
          <Form.Item
            label="组名"
            name="groupName"
          >
            <Input disabled />
          </Form.Item>
        </Col>
        <Col span={14}>
          <Form.Item
            label="组件ID"
            name="pluginId"
          >
            <Select placeholder="请选择组件ID" onChange={(e) => onSelectChange(e)}>
              {
                pluginInstallList.map((item) => {
                  const { pluginId, id } = item
                  return <Select.Option key={id} value={pluginId}>{pluginId}</Select.Option>
                })
              }
            </Select>
          </Form.Item>
        </Col>
      </Row>
      <Row>
        <Col span={10}>
          <Form.Item
            label="管道数"
            name="totalNumberOfPipline"
            rules={[{ required: true, message: '请输入管道数量' }]}
          >
            <Input />
          </Form.Item>
        </Col>
        <Col span={14}>
          <Form.Item
            label="类路径"
            name="mydataharborCreatorClazz"
          >
            <Select placeholder="请选择CreatorClazz">
              {
                clazzList.map((item, index) => {
                  return <Select.Option key={index} value={item}>{item}</Select.Option>
                })
              }
            </Select>
          </Form.Item>
        </Col>
      </Row>
      <Row>
        <Col span={10}>
          <Form.Item
            label="负载均衡"
            name="enableRebalance"
          >
            <Select placeholder="请选择是否负载均衡">
              <Select.Option value={true}>true</Select.Option>
              <Select.Option value={false}>false</Select.Option>
            </Select>
          </Form.Item>
        </Col>
        <Col span={14}>
          <Alert type="info" message="无负载均衡，如果节点停机，节点任务不会自动转移！"></Alert>
        </Col>
      </Row>
      {genTaskSetting()}
      {genTaskAction()}
      <Form.Item {...tailLayout} className="custom-footer">
        <Button htmlType="button" onClick={onReset} className>
          取消
        </Button>

        <Button type="primary" htmlType="submit">
          确认
        </Button>
      </Form.Item>
    </Form>
  </Modal>
}

export default Fork
