import { useState, useEffect } from 'react'
import { Table, Button, Modal, message, Form, Input, Select } from 'antd'
import { CloseOutlined } from '@ant-design/icons';
import { uninstallPlugin, getPlugin, getPluginList, getAllPlugin } from '../../api/index'
import Task from './task'
import InstalledPlugin from './installPlugin'

const TabPlugin = (props) => {

  const { groupName } = props

  const [visible, setVisible] = useState(false)
  const [list, setList] = useState([])
  const [taskData, setTaskData] = useState({})
  const [pluginInstallList, setPluginInstallList] = useState([])
  const [pluginInfoMap, setPluginInfoMap] = useState({})
  const [allList, setAllList] = useState([])

  useEffect(() => {
    onGetAllPlugin()
    onFetch()
    onFetchPluginList()
    return () => { }
  }, [])

  const onGetAllPlugin = async () => {
    const { data = [], result } = await getAllPlugin()
    if (result && Array.isArray(data)) {
      const curList = []
      const curMap = {}
      data.forEach(item => {
        const { plugins = [] } = item
        plugins.forEach(o => {
          const { pluginId, repoPlugins = [] } = o
          curList.push(pluginId)
          repoPlugins.forEach(col => {
            if (!curMap[pluginId]) {
              curMap[pluginId] = []
            }
            curMap[pluginId].push(col.version)
          })
        })
      })
      setAllList([...curList])
      setPluginInfoMap({
        ...curMap
      })
    }
  }

  // 初始化插件列表
  const onFetch = async () => {
    const { data, result } = await getPlugin()
    if (result) {
      setList(data[groupName] ? data[groupName].installedPlugins : [])
    }
  }

  const onFetchPluginList = async () => {
    const { data, result, msg } = await getPluginList({
      groupName: groupName
    })
    if (result) {
      setPluginInstallList(data || [])
    }
  }

  const unInstall = async (pluginId) => {
    const { data, result } = await uninstallPlugin({
      pluginId,
      groupName,
    })

    if (result) {
      message.success('插件卸载成功！')
      onFetch()
    }
  }

  const genTool = () => {
    return <div className="tool-wrap" onClick={() => { }}>
      <Button type="primary" onClick={() => setVisible(true)}>
        安装插件
      </Button>
    </div>
  }

  const genTable = () => {
    const columns = [
      {
        title: '插件ID',
        dataIndex: 'pluginId',
        key: 'pluginId',
      },
      {
        title: '描述',
        dataIndex: 'pluginDescription',
        key: 'pluginDescription',
      },
      {
        title: '类路径',
        dataIndex: 'pluginClass',
        key: 'pluginClass',
      },
      {
        title: '版本',
        dataIndex: 'version',
        key: 'version',
      },
      {
        title: 'requires',
        dataIndex: 'requires',
        key: 'requires',
      },
      {
        title: '提供者',
        dataIndex: 'provider',
        key: 'provider',
      },
      {
        title: '操作',
        key: '',
        width: 150,
        render: (text, row) => {
          return <div className="operate-tool">
            <span className="custom-btn-color1" onClick={() => setTaskData(row)}>创建任务</span>
            <span className="custom-btn-color4" onClick={() => unInstall(row.pluginId)}>
              <CloseOutlined />
              卸载
            </span>
          </div>
        }
      }
    ]

    return <Table
      columns={columns}
      dataSource={list}
      rowKey={record => record.nodeName}
    >
    </Table>
  }

  const genTask = () => {
    if (!taskData.pluginId) return
    return <Task
      config={{
        ...taskData,
        groupName
      }}
      onClose={() => setTaskData({})}
      pluginInstallList={pluginInstallList}
    >
    </Task>
  }

  const genModal = () => {
    if (!visible) return null
    return <InstalledPlugin 
      visible={visible} 
      groupName={groupName}
      onClose={() => setVisible(false)}
      onSave={() => onFetch()}
      pluginList={allList}
      pluginInfoMap={pluginInfoMap}
    >
    </InstalledPlugin>
  }

  return <div className="tab-plugin-wrap">
    {genTool()}
    {genTable()}
    {genModal()}
    {genTask()}
  </div>
}

export default TabPlugin