import React, { useState, useEffect } from 'react'
import { Table, Tag, Descriptions, Divider, Modal, Form, Input, Select, Button } from 'antd'
import { getTaskList, getPluginList } from '../../api/index'
import { TaskState } from '../../utils/config'
import { JsonEditor as Editor } from 'jsoneditor-react';
import Edit from './edit'
import Fork from './fork'
import Stauts from './status'
import 'jsoneditor-react/es/editor.min.css';
import './index.scss'

const Task = (props) => {

  const [data, setData] = useState([])
  const [detailData, setDetailData] = useState({})
  const [editData, setEditData] = useState({})
  const [forkData, setForkData] = useState(false)
  const [statusData, setStatusData] = useState(false)
  const [pluginInstallList, setPluginInstallList] = useState([])

  useEffect(() => {
    onFetch()
    onFetchPluginList()
  }, [])

  const onFetch = async () => {
    const { data, result } = await getTaskList()
    if (result) {
      const list = []
      Object.keys(data).forEach((key, index) => {
        list.push({
          ...data[key],
          index,
          operateIndex: index + '_operate'
        })
      })
      setData(list)
    }
  }

  const onFetchPluginList = async () => {
    const { groupName } = props 
    const { data, result, msg } = await getPluginList({
      groupName: groupName
    })
    if (result) {
      setPluginInstallList(data || [])
    }
  }

  const genTable = () => {
    const columns = [
      {
        title: '任务ID',
        dataIndex: 'taskId',
        key: 'taskId',
      },
      {
        title: '状态',
        width: 100,
        key: 'taskState',
        render: (text, row) => {
          const { title, color } = TaskState[row.taskState]
          return <Tag color={color}>
            {
              title
            }
          </Tag>
        }
      },
      {
        title: '组名',
        dataIndex: 'groupName',
        key: 'groupName',
      },
      {
        title: '负载均衡',
        width: 100,
        dataIndex: 'index',
        key: 'index',
        render: (status, row) => {
          return <span className={`${row.enableRebalance ? 'success' : 'warning'}`}>
            {
              String(row.enableRebalance)
            }
          </span>
        }
      },
      {
        title: '管道数',
        width: 100,
        dataIndex: 'totalNumberOfPipline',
        key: 'totalNumberOfPipline',
      },
      {
        title: '操作',
        key: '',
        width: 200,
        render: (text, row) => {
          return <div className="operate-tool">
            <span className="custom-btn-color1" onClick={() => setDetailData(row)}>详情</span>
            <span className="custom-btn-color2" onClick={() => setEditData(row)}>修改</span>
            <span className="custom-btn-color3" onClick={() => setForkData(row)}>fork</span>
            <span className="custom-btn-color4" onClick={() => setStatusData(row)}>状态</span>
          </div>
        }
      },
    ]
    return <Table
      columns={columns}
      dataSource={data}
      rowKey={record => record.index}
    >
    </Table>
  }

  const genBasicInfo = () => {
    return <Descriptions title="基本信息" className="custom-descriptions">
      <Descriptions.Item label="组名">
        {detailData.groupName}
      </Descriptions.Item>
      <Descriptions.Item label="管道数">
        {detailData.totalNumberOfPipline}
      </Descriptions.Item>
      <Descriptions.Item label="负载均衡">
        <span className={`${detailData.enableRebalance ? 'success' : 'warning'}`}>
          {
            String(detailData.enableRebalance)
          }
        </span>
      </Descriptions.Item>

      <Descriptions.Item label="任务ID">
        {detailData.taskId}
      </Descriptions.Item>
      <Descriptions.Item label="插件ID">
        {detailData.pluginId}
      </Descriptions.Item>
    </Descriptions>
  }

  const genTaskInfo = () => {
    const { taskAssignedInfo = {} } = detailData
    const { assignedInfoMap } = taskAssignedInfo
    let params = {}
    for (let key in assignedInfoMap) {
      params = assignedInfoMap[key]
    }
    return <Descriptions title="任务分配信息" className="custom-descriptions">
      <Descriptions.Item label="节点">
        {params.nodeName}
      </Descriptions.Item>
      <Descriptions.Item label="任务数">
        {params.taskNum}
      </Descriptions.Item>
      <Descriptions.Item label="diverted">
        <span className={`${params.diverted ? 'success' : 'warning'}`}>
          {
            String(params.diverted)
          }
        </span>
      </Descriptions.Item>
      <Descriptions.Item label="创建异常">
        {params.createException}
      </Descriptions.Item>
    </Descriptions>
  }

  const genPipeInfo = () => {
    const { taskAssignedInfo = {} } = detailData
    const { assignedInfoMap } = taskAssignedInfo
    let params = {}
    for (let key in assignedInfoMap) {
      params = assignedInfoMap[key]
    }
    const { piplineStates = {}, writeTotal = {} } = params
    return <div className="custom-descriptions">
      <Descriptions title="管道信息">
        {
          Object.keys(piplineStates).map(key => {
            return <Descriptions.Item label={key} key={key} span={3}>
              {piplineStates[key]}
            </Descriptions.Item>
          })
        }
      </Descriptions>
      <Divider />
      <Descriptions title="写总计">
        {
          Object.keys(writeTotal).map(key => {
            return <Descriptions.Item label={key} key={key} span={3}>
              {writeTotal[key]}
            </Descriptions.Item>
          })
        }
      </Descriptions>
    </div>
  }

  const genTaskSetting = () => {
    const settingJson = JSON.parse(detailData.settingJsonConfig)
    return <React.Fragment>
      <div className="custom-title">任务设置</div>
      <Editor
        value={settingJson}
        onChange={() => { }}
      />
    </React.Fragment>
  }

  const genTaskAction = () => {
    const configJson = JSON.parse(detailData.configJson)
    return <React.Fragment>
      <div className="custom-title">任务行为</div>
      <Editor
        value={configJson}
        onChange={() => { }}
      />
    </React.Fragment>
  }

  const genDetail = () => {
    if (!detailData.pluginId) return
    return <Modal
      visible={detailData.pluginId}
      width={800}
      title={`任务详情`}
      onCancel={() => setDetailData({})}
      footer={null}
    >
      {genBasicInfo()}
      {genTaskInfo()}
      {genPipeInfo()}
      {genTaskSetting()}
      {genTaskAction()}
    </Modal>
  }

  const genEdit = () => {
    return <Edit 
      {...editData}
      onClose={() => setEditData({})}
      onSave={() => onFetch()}
    >
    </Edit>
  }

  const genFork = () => {
    if (!forkData.pluginId) return
    return <Fork 
      config={forkData}
      onClose={() => setForkData({})}
      onSave={() => onFetch()}
      pluginInstallList={pluginInstallList}
    >
    </Fork>
  }

  const genStatus = () => {
    return <Stauts
      config={statusData}
      onClose={() => setStatusData({})}
      onSave={() => onFetch()}
    >
    </Stauts>
  }

  return <div className="task-wrap">
    {genTable()}
    {genDetail()}
    {genEdit()}
    {genFork()}
    {genStatus()}
  </div>
}

export default Task