import { useState, useEffect } from 'react'
import { Tabs, Select } from 'antd'
import TabNode from './tabNode'
import TabPlugin from './tabPlugin'
import TabTask from '../Task/index'
import { getGroupList } from '../../api/index'
import './index.scss'

const { TabPane } = Tabs;
const { Option } = Select

const Cluster = (props) => {

  const [activeKey, setActiveKey] = useState('1')
  const [clusterParam, setClusterParam] = useState({})
  const [list, setList] = useState([])
  const [value, setValue] = useState('')

  useEffect(() => {
    onFetch()
  }, [])

  const onFetch = async () => {
    const { data, result } = await getGroupList()
    if (result) {
      setClusterParam({
        ...data
      })
      const keyList = Object.keys(data)
      setList(keyList.map(key => {
        return {
          key: key,
          title: key
        }
      }))
      setValue(keyList.length > 0 ?  keyList[0] : '')
    }
  }

  const callback = (key) => {
    setActiveKey(key)
  }

  const operations = {
    right: <div className="header-wrap">
      <Select value={value} placeholder="切换集群" className="custom-select" onChange={setValue}>
        {
          list.map((item, inx) => {
            const { key, title } = item
            return <Option key={ key }>{title}</Option>
          })
        }
      </Select>
    </div>
  }

  return <div className="cluster-wrap">
    <Tabs activeKey={activeKey} onChange={callback} tabBarExtraContent={operations} className="custom-tab">
      <TabPane tab="节点" key="1">
        {
          value && clusterParam[value] ? <TabNode list={ clusterParam[value].nodeInfos || []}></TabNode> : null
        }
      </TabPane>
      <TabPane tab="插件" key="2">
        <TabPlugin groupName={value}></TabPlugin>
      </TabPane>
      <TabPane tab="任务" key="3">
        <TabTask groupName={value}></TabTask>
      </TabPane>
    </Tabs>
  </div>
}
export default Cluster