import { Modal, Form, Input, Select, Button, message} from 'antd'
import { editTask } from '../../api/index'

const Edit = (props) => {
  const [form] = Form.useForm();
  const { onClose, onSave } = props
  const params = {
    taskId: props.taskId,
    totalNumberOfPipline: props.totalNumberOfPipline,
    enableRebalance: props.enableRebalance,
  }
  const tailLayout = {
    wrapperCol: { offset: 0, span: 24 },
  };

  const onFinish = async (values) => {
    const { data, result, msg } = await editTask(values)
    if (result) {
      message.success('修改成功')
      onSave()
      onClose()
    } else {
      message.error(msg)
    }
  }

  const onReset = () => {
    onClose()
  }

  return <Modal
    visible={props.pluginId}
    title={`任务修改`}
    onCancel={() => onClose() }
    footer={null}
  >
    <Form
      form={form}
      labelCol={{ span: 4 }}
      wrapperCol={{ span: 20 }}
      initialValues={params}
      onFinish={onFinish}
    >
      <Form.Item
        label="任务ID"
        name="taskId"
      >
        <Input disabled />
      </Form.Item>
      <Form.Item
        label="管道数"
        name="totalNumberOfPipline"
        rules={[{ required: true, message: '请输入管道数量' }]}
      >
        <Input type="number" />
      </Form.Item>
      <Form.Item
        label="负载均衡"
        name="enableRebalance"
      >
        <Select>
          <Select.Option value={true}>true</Select.Option>
          <Select.Option value={false}>false</Select.Option>
        </Select>
      </Form.Item>
      <Form.Item {...tailLayout} className="custom-footer">
        <Button htmlType="button" onClick={onReset} className>
          取消
        </Button>
        
        <Button type="primary" htmlType="submit">
          确认
        </Button>
      </Form.Item>
    </Form>
  </Modal>
}

export default Edit
