import moment from 'moment'
import { Table, Tag } from 'antd'
import { formatDuring } from '../../utils/tools'
import { TaskState } from '../../utils/config'

const TabNode = (props) => {
  const { list = [] } = props

  const genTable = () => {
    const columns = [
      {
        title: '节点名',
        dataIndex: 'nodeName',
        key: 'nodeName',
        width: 130
      },
      {
        title: 'IP',
        dataIndex: 'ip',
        key: 'ip',
      },
      {
        title: '端口',
        dataIndex: 'port',
        key: 'port',
      },
      {
        title: 'hostName',
        dataIndex: 'hostName',
        key: 'hostName',
      },
      {
        title: '运行时间',
        dataIndex: 'startTime',
        key: 'startTime',
        render: (time, row) => {
          return formatDuring(time)
        }
      },
      {
        title: '任务数',
        dataIndex: 'taskNum',
        key: 'taskNum',
      },
      {
        title: 'isLeader',
        key: 'leader',
        render: (text, row) => {
          return <Tag color={ text ? 'success' : 'error'}>
            {
              text ? 'true' : 'false'
            }
          </Tag>
        }
      },
      {
        title: 'osName',
        dataIndex: 'osName',
        key: 'osName',
      },
      {
        title: 'osArch',
        dataIndex: 'osArch',
        key: 'osArch',
      },
    ]
    return <Table
      columns={columns}
      dataSource={list}
      rowKey={record => record.nodeName}
    >
    </Table>
  }
  
  return genTable()
}

export default TabNode