import React, { useState, useEffect } from 'react'
import { Table, Button, Modal, message, Input, Upload } from 'antd'
import { getAllPlugin } from '../../api/index'
import { SearchOutlined, ArrowLeftOutlined, UploadOutlined } from '@ant-design/icons';
import './index.scss'
const Plugin = () => {

  const [list, setList] = useState([])
  const [dataSource, setDataSource] = useState([])
  const [showTable, setShowTable] = useState(false)
  const [visible, setVisible] = useState(false)

  useEffect(() => {
    onFetch()
    return () => { }
  }, [])


  const onFetch = async () => {
    const { data = [], result } = await getAllPlugin()
    if (result && Array.isArray(data)) {
      setList([...data])
    }
  }

  const onSave = () => {

  }

  const onBeforeUpload = (file, fileList) => {
    if (file.size < 500 * 1024 * 1024) {
      return true
    } else {
      message.error('文件不能超过500M')
      return false
    }
  }

  const onChange = (params) => {
    const { file } = params
    const { status, response = {} } = file
    const { code, msg } = response
    if (status === 'done') {
      if (code == 0) {
        message.success('上传成功')
        setVisible(false)
        onFetch()
      } else {
        message.error(msg)
      }
    }
  }

  const onItemClick = (plugins) => {
    setShowTable(true)
    const curList = []
    plugins.forEach(item => {
      const { repoPlugins = [] } = item
      repoPlugins.forEach(o => {
        curList.push(o)
      })
    })
    setDataSource(curList)
  }

  const genTools = () => {
    return <div className="tool-wrap">
      <div className="left" onClick={() => setShowTable(false)}>
        <span className="back">
          <ArrowLeftOutlined />
          返回
        </span>
        {/* <Input className="custom-input" placeholder="插件id"></Input>
        <Input className="custom-input" placeholder="插件版本"></Input>
        <Input className="custom-input" placeholder="插件描述"></Input>
        <Button icon={<SearchOutlined />} type="primary">搜索</Button> */}
      </div>
      <div className="right">
        <Button type="primary" onClick={() => setVisible(true)}>上传插件</Button>
      </div>
    </div>
  }

  const genTable = () => {
    const columns = [
      {
        title: '插件id',
        dataIndex: 'pluginId',
        key: 'pluginId',
      },
      {
        title: '版本',
        dataIndex: 'version',
        key: 'version',
      },
      {
        title: '文件名',
        dataIndex: 'fileName',
        key: 'fileName',
      },
      {
        title: '描述',
        dataIndex: 'pluginDescription',
        key: 'pluginDescription',
      },
      {
        title: '提供人',
        dataIndex: 'provider',
        key: 'provider',
      }
    ]

    return <React.Fragment>
      {genTools()}
      <Table
        columns={columns}
        dataSource={dataSource}
        rowKey={record => record.nodeName}
      >
      </Table>
    </React.Fragment>
  }

  const genList = () => {
    return <div className="list-wrap">
      {
        list.map((item, index) => {
          const { groupName, imageBase64, plugins, repoName, repoType } = item
          return <div className="item" onClick={() => onItemClick(plugins)} key={index}>
            <div className="inner">
              {
                imageBase64 ?
                  <img src={imageBase64} className="img" ></img>
                  :
                  <div className="img-tip">{groupName}</div>
              }
              <div className="title">
                {repoName}
              </div>
            </div>
          </div>
        })
      }
    </div>
  }

  const genModal = () => {
    return <Modal
      visible={visible}
      title={'插件上传'}
      footer={null}
      onCancel={() => setVisible(false)}
    >
      <Upload
        name="file"
        action="mydataharbor/plugin/uploadPlugin"
        accept=".jar"
        beforeUpload={onBeforeUpload}
        onChange={onChange}
      >
        <Button icon={<UploadOutlined />} type="primary">上传插件</Button>
        <div className="custom-tip">只能上传jar文件，且不超过500M</div>
      </Upload>
    </Modal>
  }

  return <div className="plugin-wrap">
    {
      showTable ? genTable() : genList()
    }
    {genModal()}
  </div>
}

export default Plugin