import React, { useState } from 'react'
import { Switch, Route,  Redirect } from 'react-router-dom'
import { Layout, Menu } from 'antd';
import { MenuUnfoldOutlined, MenuFoldOutlined, BookFilled, GithubFilled } from '@ant-design/icons';
import Footer from './components/Footer'
import { RouterConfig, ExactLink } from "./utils/config";
import './App.scss';

const { Header, Sider, Content } = Layout;
const MenuItem = Menu.Item

const App = props => {
  const { history } = props
  const [collapsed, setCollapsed] = useState(false)
  const [selectedKeys, setSelectedKeys] = useState(['home'])

  const onToggle = () => {
    setCollapsed((pre) => !pre)
  }

  const onMenuChange = (e) => {
    history.push(`/${e.keyPath[0]}`)
    setSelectedKeys(e.keyPath)
  }

  const genMenuItem = () => {
    return RouterConfig.map((item) => {
      const { title, key, icon: Icon } = item
      return <MenuItem key={key} icon={<Icon />}>
        {title}
      </MenuItem>
    })
  }

  const goPage = (url) => {
    window.open(url)
  }

  const genRouter = () => {
    return <Switch>
      {
        RouterConfig.map((item) => {
          const { key, component } = item
          return <Route key={key} path={`/${key}`} component={component} exact></Route>
        })
      }
      <Redirect from="/" to="/home"></Redirect>
    </Switch>
  }

  return <div className="app-wrap">
    <Layout className="custom-layout">
      <Sider className="custom-slider" trigger={null} collapsible collapsed={collapsed}>
        <div className="logo">
          {
            !collapsed ? <span className="data">MyData</span> : ''
          }
          <span className="label">Harbor</span>
        </div>
        <Menu theme="dark" mode="inline" selectedKeys={selectedKeys} onClick={onMenuChange}>
          {genMenuItem()}
        </Menu>
      </Sider>
      <Layout className="site-layout">
        <Header className="site-layout-header" style={{ padding: 0 }}>
          {
            collapsed ? 
            <MenuUnfoldOutlined  className="trigger" onClick={onToggle}/> 
            : <MenuFoldOutlined className="trigger" onClick={onToggle} />
          }
          <div className="right">
            <BookFilled className="custom-label" onClick={() => goPage(ExactLink.bbs)} />
            <GithubFilled className="custom-label" onClick={() => goPage(ExactLink.github)} />
          </div>
        </Header>
        <Content
          className="site-layout-content"
        >
          {
            genRouter()
          }
          <Footer></Footer>
        </Content>
      </Layout>
    </Layout>
  </div>
}

export default App;
