import { useState } from 'react'
import { Modal, Input, Select, Form, message, Button } from 'antd'
import { installPlugin } from '../../api/index'
const InstallPlugin = (props) => {

  const { visible, onClose, onSave, pluginList, pluginInfoMap, groupName } = props
  const [currentPlugin, setCurrentPlugin] = useState('')

  const [form] = Form.useForm();
  const params = {
    groupName: groupName,
    pluginId: '',
    version: '',
    sync: false
  }
  const tailLayout = {
    wrapperCol: { offset: 0, span: 24 },
  }

  const onFinish = async (values) => {
    const { data, result, msg } = await installPlugin({
      ...params,
      ...values
    })
    if (result) {
      message.success('插件安装成功！')
      onClose()
      onSave()
    } else {
      message.error(msg)
    }
  }

  return <Modal visible={visible} title="安装插件" onCancel={() => onClose()} footer={null}>
    <Form form={form} labelCol={{ span: 8 }} autoComplete="off" initialValues={params} onFinish={onFinish}>
      <Form.Item
        label="组名"
        name="groupName"
      >
        <Input disabled />
      </Form.Item>
      <Form.Item
        label="组件ID"
        name="pluginId"
        rules={[{ required: true, message: '请选择组件版本' }]}
      >
        <Select placeholder="请选择组件ID" onChange={(v) => setCurrentPlugin(v)}>
          {
            pluginList.map((item) => {
              return <Select.Option key={item} value={item}>{item}</Select.Option>
            })
          }
        </Select>
      </Form.Item>
      <Form.Item
        label="版本"
        name="version"
        rules={[{ required: true, message: '请选择组件版本' }]}
      >
        <Select placeholder="请选择组件版本">
          {
            (pluginInfoMap[currentPlugin] || []).map((item) => {
              return <Select.Option key={item} value={item}>{item}</Select.Option>
            })
          }
        </Select>
      </Form.Item>
      <Form.Item {...tailLayout} className="custom-footer">
        <Button htmlType="button" onClick={() => onClose()} className>
          取消
    </Button>

        <Button type="primary" htmlType="submit">
          确认
    </Button>
      </Form.Item>
    </Form>
  </Modal>
}
export default InstallPlugin