import { Modal, Form, Input, Select, Button, message} from 'antd'
import { stateList } from '../../utils/config'
import { statusTask } from '../../api/index'


const Status = (props) => {
  const [form] = Form.useForm();
  const { onClose, onSave, config } = props
  const params = {
    taskId: config.taskId,
    taskState: config.taskState,
  }
  const tailLayout = {
    wrapperCol: { offset: 0, span: 24 },
  };

  const onFinish = async (values) => {
    const { data, result, msg } = await statusTask(values)
    if (result) {
      message.success('修改成功')
      onSave()
      onClose()
    } else {
      message.error(msg)
    }
  }

  const onReset = () => {
    onClose()
  }

  return <Modal
    visible={config.pluginId}
    title={`任务状态`}
    onCancel={() => onClose() }
    footer={null}
  >
    <Form
      form={form}
      labelCol={{ span: 4 }}
      wrapperCol={{ span: 20 }}
      initialValues={params}
      onFinish={onFinish}
    >
      <Form.Item
        label="任务ID"
        name="taskId"
      >
        <Input disabled />
      </Form.Item>
      <Form.Item
        label="任务状态"
        name="taskState"
      >
        <Select>
          {
            stateList.map(item => {
              return  <Select.Option key={item} value={item}>{item}</Select.Option>
            })
          }
        </Select>
      </Form.Item>
      <Form.Item {...tailLayout} className="custom-footer">
        <Button htmlType="button" onClick={onReset} className>
          取消
        </Button>
        
        <Button type="primary" htmlType="submit">
          确认
        </Button>
      </Form.Item>
    </Form>
  </Modal>
}

export default Status
